    text-align: left;
    white-space: nowrap;
    color: #666;
    font-weight: normal;
}
.table td {
    color: #333;
}
.table th:first-child,
  .table td:first-child {
    padding-left: 10px;
}
h2 {
  margin: 2.5rem 0 1rem 0;
}
.content a {
  text-decoration: none;
  color: #007bff;
}
#app .container {
  width: 1450px;
  margin: 0 auto;
  padding: 1rem;
  background-color: #ffffff;
}

.side-nav {
  margin: 0 0.5em;
  border-right: 1px solid #dee2e6;
  font-size: 1.1rem;
}

.node {
  list-style: none;
  margin-left: 0;
  padding: 0;
  margin: 0.5rem 0;
}
.node a {
    display: block;
    text-decoration: none;
    transition: all 0.4s ease-in-out;
    margin: 1rem 0;
}
.node a:hover .title, .node a.router-link-active .title {
      color: #007bff;
}
.node a.router-link-active {
      background-color: #99caff;
      border-right: 2px solid #007bff;
}
.node .title {
    transition: all 0.4s ease-in-out;
    font-size: 0.9em;
    font-weight: normal;
    margin: 0;
    padding: 0.5rem 0;
    color: #212529;
}
.node .title .xl-icon + .title__inner {
      padding-left: 0.5rem;
}
.sub-node .node a {
  padding-left: 1rem;
}

.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: #fff;
  margin-bottom: 1em;
  z-index: 1;
  box-shadow: 0 0 0.5em #dee2e6;
  min-width: 1250px;
}
.header .header__wrapper {
    position: relative;
    min-width: 1210px;
    margin: 0 20px;
    height: 60px;
}
.header .logo {
    position: relative;
    text-decoration: none;
    color: #007bff;
}
.header .logo h1 {
      display: inline-block;
      text-align: center;
      margin: 0;
      padding: 0;
      height: 60px;
      line-height: 60px;
}
.header .right {
    position: relative;
    float: right;
    height: 60px;
    display: table;
}
.header .right > * {
      display: table-cell;
      vertical-align: middle;
}

.demo-block {
  border: 1px solid #dee2e6;
  font-size: 1rem;
  transition: box-shadow 0.2s ease-in-out;
  border-radius: 0.25rem;
}
.demo-block code {
    font-family: Menlo, Monaco, Consolas, Courier, monospace;
}
.demo-block:hover {
    box-shadow: 0 0 0.5rem 0 #dee2e6;
}
.demo-block .demo-code {
    padding: 1.5rem;
    border-bottom: 1px solid #dee2e6;
}
.demo-block .demo-desc {
    position: relative;
    padding: 0.5rem 2rem 0.5rem 1.5rem;
}
.demo-block .demo-desc .code-btn {
      position: absolute;
      right: 0.5rem;
      top: 0.5rem;
}
.demo-block .demo-desc .demo-desc__icon {
      display: block;
      cursor: pointer;
      color: #888;
      font-size: 2rem;
      transform-origin: 50% 50%;
      transition: transform 0.4s ease-in-out;
}
.demo-block .demo-desc p {
      margin: 0;
      padding: 0;
      line-height: 2;
}
.demo-block .demo-desc code {
      background-color: #e6effb;
      display: inline-block;
      margin: 0 0.2rem;
      padding: 0.1rem 0.4rem;
      height: 1.1rem;
      line-height: 1.1rem;
}
.demo-block .demo-source {
    position: relative;
    border-top: 1px dashed #dee2e6;
}
.demo-block .demo-source pre {
      margin: 0;
}
.demo-block .demo-source .code-form {
      position: absolute;
      display: block;
      right: 0.3em;
      top: 0.3em;
      color: #888;
}
.demo-block .demo-source .demo-source__icon {
      font-size: 1.5rem;
      padding: 0.5rem;
      cursor: pointer;
}
.demo-block .demo-source .demo-source__icon:hover {
        color: #007bff;
}
.demo-block .icon-fade-enter-active,
  .demo-block .icon-fade-leave-active {
    transition: opacity 0.4s ease-in-out;
}
.demo-block .icon-fade-enter,
  .demo-block .icon-fade-leave-to {
    opacity: 0;
}
.demo-block .icon-fade-enter-to,
  .demo-block .icon-fade-leave {
    opacity: 1;
}
.demo-block .collapse-transition {
    transition: all 0.4s ease-in-out;
}
